.home-box3 {
    padding: 20px;
}

.home-img {
    max-width: 30%;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 0px 17px -3px rgba(0, 0, 0, 0.75);
    -webkit-box-shadow: 0px 0px 17px -3px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 0px 17px -3px rgba(0, 0, 0, 0.75);
}

.home-box5 {
    display: flex;
    align-items: center;
    justify-content: center;
}

.home-box2 {
    padding: 50px;
}

.home-p {
    padding: 20px;
    text-align: center;
    font-size: calc(100% + 50%);
    color: white;
    /* width: calc(100% - 30%); */
    letter-spacing: 2px;
    
}
.home-p span{
    font-size: calc(100% + 80%);
    color:#db2a1b ;
    font-weight: 900;

}

.home-p2 {
    color: #000;
    padding: 20px;
    text-align: center;
    font-size: calc(100% + 10%);
}

.home-box {
    /* background: url(/src/Assets/Images/background.png) center bottom / cover; */
    background-color: #8BC6EC;
background-image: linear-gradient(135deg, #8BC6EC 0%, #9599E2 100%);

    z-index: 10;
}

.home-box2 {
    background: hsl(0 0% 100% / 0.1);
    backdrop-filter: blur(0.5rem);
    z-index: 20;
}

.home-box6 {
    /* min-width: 90%; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.home-p4{
    font-size: 500%;
}

.home-box6 a{
    border-radius: 10px;
    text-decoration: none;
    color: #fff;
    background-color:#db2a1b;
    padding: 1rem 1.5rem;
    transition: 0.5s ease-in-out;
}

.home-box6 a:hover{
    background-color:rgb(225,88,19);

}