.roadmap{
    display: grid;
    grid-template-columns: auto auto auto;
    padding: 10px;
}

 h1{
    transform: translateX(40%);
    font-size: 32px;
}


.roadmap-card{
    box-shadow: 0px 0px 17px -3px rgba(0, 0, 0, 0.75);
    -webkit-box-shadow: 0px 0px 17px -3px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 0px 17px -3px rgba(0, 0, 0, 0.75);
    padding: 0.5rem;
    margin: 1rem;
}

.roadmap-card h2{
    color: #000;
}